import Header from "./Header";
import Footer from "./Footer";

const layoutStyle = {
  margin: 10,
  padding: 10,
  border: "1px solid #DDD"
};

const contetStyle = {
  backgroundColor: "gray"
};

export default function Layout(props) {
  return (
    <div style={layoutStyle}>
      <Header />
      <div style={contetStyle}>{props.children}</div>
      <Footer />
    </div>
  );
}
